	module.exports = {"data":{"post":{"html":"<p><b>Noon</b><br />\nChristopher has a fat lip. He slipped from the couch and landed with his lip on the corner of Mama's old doll house. There was some blood at first as two teeth cut into his lip, but he's eating and drinking just fine. He just looks like a battered boxer.</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/03/crhboxer.jpg\" height=\"244\" width=\"325\" border=\"0\" alt=\"crhboxer.jpg: \" /></p>\n<p><b>Early</b><br />\nSomething is very wrong with my internal body clock I used to to think that 5 am was a time of day that existed only in limbo. Now, even though I can sleep until 5:45 on work days, I'm always awake before 5:00. Even on weekends, when I can sleep in.</p>","fields":{"title":"Breaking Things","date":"17 Mar 2001","url":"https://papascott.de/2001-03-17-breaking-things/"}}},"pathContext":{"slug":"/2001-03-17-breaking-things/","prev":{"fields":{"title":"Dot Bomb","date":"15 Mar 2001","slug":"/2001-03-15-dot-bomb/"}},"next":{"fields":{"title":"Overload","date":"19 Mar 2001","slug":"/2001-03-19-overload/"}}}}

